            "Username": "Nazwa uytkownika:",
            "Password": "Haso:",
            "Log In": "Zaloguj si",
            "Sign Up": "Zarejestruj si",
            "Username null": "Nazwa uytkownika jest wymagana.",
            "Password null": "Haso jest wymagane.",
            "Email null": "Email jest wymagany.",
